<template>
  <div class="cart">
    <div class="cart-body">
      <div class="cart-title">–ö–æ—Ä–∑–∏–Ω–∞</div>
      <div class="cart-total">
        –û–±—â–∞—è —Å—É–º–º–∞: <span>{{ TotalPrice(cards) }}</span> —Ä—É–±
      </div>
      <div class="cart-wrapper">
        <card-in-cart-component />
      </div>
      <button @click.prevent="onSubmitCard(cards)" class="btn btn-primary cart-confirm">–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑</button>
      <slot></slot>
    </div>
  </div>
</template>

<script>
import CardInCartComponent from "@/components/CardInCartComponent.vue";
import { doc, setDoc } from "firebase/firestore"

export default {
	data() {
		return {
			timer: null
		}
	},
  components: { CardInCartComponent },
  computed: {
    cards() {
        return this.$store.getters["getCards"];
      
    },
  },
  methods: {
    TotalPrice(cards) {
        return cards.reduce((res, cur) => {
          return res + cur.price;
        }, 0);
      
    },
    onSubmitCard(cards) {
	
		localStorage.removeItem('cards')
		this.$store.dispatch("setCardsLocalStorage", [])
		this.timer = setTimeout(() => {
			alert("–°–ø–∞—Å–∏–±–æ, –¥–∞–Ω–Ω—ã–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã! –ù–∞—à –º–µ–Ω–µ–¥–∂–µ—Ä —Å–≤—è–∂–µ—Ç—Å—è —Å –≤–∞–º–∏")
		},1000)
		
    }
  },
  destroyed() {
	clearTimeout(this.timer)
  }
};
</script>
